  <div :class="{'signup-feedback-container': isPc, 'signup-feedback-container-mb': !isPc}">
    <div class="signup-feedback-content">
      <i class="success-icon" :style="{backgroundImage: 'url(' + $assetsUrl + 'user/feedback.svg)'}"></i>
      <p class="success-title">註冊成功</p>
      <p class="success-description">請在移動端App登入您所註冊的帳號<br>或直接登入後台。</p>
      <a-button type="primary" html-type="submit" :loading="iconLoading" class="signup-feedback-button"
