<script>
  import Loading from '@/components/loading.vue';

  export default {
    name: 'interchange',
    components: {
      Loading,
    },
    data() {
      return {
        iconLoading: false,
        isPc: this.$isPc()
      };
    },
    created() {
    },
    mounted() {
      this.isAndroid = (navigator.userAgent.toLowerCase().indexOf('android') > -1);
      // Send the event to GTM
      if (!!localStorage.getItem('token')) {
        window.dataLayer.push({'event': 'signUpCompleted'})
      }

      if (this.isAndroid) {
        this.iconLoading = true
        const that = this;
        setTimeout(function () {
          that.iconLoading = false;
          window.location.href = `whaleblue://test.com`;
          setTimeout(function () {
            window.location.href = `whaleblue://test.com`;
          }, 2000);
        }, 2000);
      }
      let exoclick = document.createElement("div");
      exoclick.setAttribute('style', 'display: none; visibility: hidden')
      exoclick.innerHTML = `<script type="application/javascript" src="https://a.exoclick.com/tag_gen.js" data-goal="5f3934ee3fb895723593751c8889a35a"><\/script>`
      document.body.appendChild(exoclick)
    },
    methods: {
      gotoDashboard() {
        window.open(`${window.location.origin}/my/service`, '_self');
      }
    }
  };
</script>

